/*基本样式表*/
*{
	padding: 0;
	margin: 0;
}
body{
	font-size: 12px;
	background: rgba(255,255,255,0.9);
}
ul li,ol li{
	list-style: none;
}
a{
	text-decoration: none;
    color: #2db7f5;
    cursor: pointer;
    outline: medium none;
    transition: color 0.3s ease 0s;
}



